import React from 'react';
import { fireEvent, render } from '@testing-library/react-native';
import { Home } from '../Home/Home';
import { QueryClient, QueryClientProvider } from 'react-query';
import { HomeScreenNavigationProp } from 'navigation/types';
import { ThemeProvider } from 'styled-components/native';
import mockTheme from '../../../__mocks__/themeProvider';
import { currenciesData, userData } from '../../../__mocks__/data';

jest.mock('../../hooks/useUser', () => ({
  useUser: jest.fn(() => ({
    clearUser: jest.fn(),
    user: { idToken: '13456-ID',
		serverAuthCode: '1234',
		user: {
			email: 'nerio@challenge.com',
			familyName: 'Testing',
			givenName: 'Challenge',
			id: '1',
			name: 'test',
			photo: 'https://www.test.com/photo.png',
		},},
  })),
}));

const mockData = { data: currenciesData, isLoading: false };
jest.mock('react-query', () => ({
  ...jest.requireActual('react-query'),
  useQuery: () => mockData,
}));

jest.mock('../../hooks/useFavorites', () => ({
  useFavorites: () => ({
    addFavorite: jest.fn(),
    isFavorite: jest.fn(),
    removeFavorite: jest.fn(),
    favorites: [],
  }),
}));

const mockNavigation: HomeScreenNavigationProp = {
  navigate: jest.fn(),
  dispatch: jest.fn(),
  reset: jest.fn(),
  goBack: jest.fn(),
  isFocused: jest.fn(),
  canGoBack: jest.fn(),
  getParent: jest.fn(),
  getState: jest.fn(),
  setParams: jest.fn(),
  getId: jest.fn(),
  setOptions: jest.fn(),
  addListener: jest.fn(),
  removeListener: jest.fn(),
  replace: jest.fn(),
  push: jest.fn(),
  pop: jest.fn(),
  popToTop: jest.fn(),
};

const renderHomeComponent = () => {
  return render(
      <ThemeProvider theme={mockTheme}>
        <Home navigation={mockNavigation} />
      </ThemeProvider>
  );
};

describe('Home Component', () => {
  it('should render a snapshot with data', () => {
    const { toJSON } = renderHomeComponent();
    expect(toJSON()).toMatchSnapshot();
  });

  it('should navigate when Item press', () => {
    const { getByText } = renderHomeComponent();
    fireEvent.press(getByText('Bitcoin'));
    expect(mockNavigation.navigate).toHaveBeenCalledWith('Detail', {
      currency: currenciesData[0],
    });
  });
});
